<template>
	<el-row>
		<el-col :span="6"><div class="grid-content"></div></el-col>
		<el-col :span="12"><div class="grid-content">
			<el-table
				:data="comments"
				stripe
				style="width: 100%">
				<el-table-column
					prop="user"
					label="用户名"
					width="180">
				</el-table-column>
				<el-table-column
					prop="content"
					label="内容">
				</el-table-column>
				<el-table-column
					fixed="right"
					label="操作"
					width="100">
					<template slot-scope="scope">
						<el-button @click="del(scope.row.id)" icon="el-icon-delete" type="info" circle></el-button>
					</template>
				</el-table-column>
			</el-table>
		</div></el-col>
		<el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
	</el-row>
</template>

<script>
	export default {
		props: [
			"comments"
		],
		methods: {
			del(id) {
				this.$emit('delComment', id);
			}
		}
	}
</script>