<template>
	<el-row class="cinput">
		<el-col :span="6"><div class="grid-content"></div></el-col>
		<el-col :span="12"><div class="grid-content">
			<el-form ref="form" :model="form" label-width="80px">
				<el-form-item label="用户名：">
					<el-input v-model="form.user" clearable></el-input>
				</el-form-item>
				<el-form-item label=" 内 容 ：">
					<el-input type="textarea" autosize v-model="form.content"></el-input>
				</el-form-item>
				<el-form-item></el-form-item>
				<el-button type="primary" plain @click="add">发布</el-button>
			</el-form>
		</div></el-col>
		<el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
	</el-row>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					user: '',
					content: ''
				}
			}
		},
		methods: {
			add() {
				this.$emit("addcomment",{
					id: +new Date(),
					user: this.form.user,
					content: this.form.content
				});
				localStorage.setItem('userName', this.form.user);
				this.form.content = '';
			}
		},
		created() {
        const user = localStorage.getItem('userName');
        if (user) {
            this.form.user = user;
        }
    }
	};
</script>