<template>
	<div>
		<c-input @addcomment="additem"></c-input>
		<c-list @delComment="delitem" :comments="comments"></c-list>
	</div>
</template>

<script>
	import cList from './cList.vue';
	import cInput from './cInput.vue';
	
	export default { 
		data() {
			return {
				comments: []
			}
		},
		components: {
			'c-input': cInput,
			'c-list': cList
		},
		methods: {
			additem(data) {
				this.comments.push(data);
				this.updateLocal();
			},
			delitem(id) {
				this.comments = this.comments.filter((c) => c.id != id);
				this.updateLocal();
			},
			updateLocal() {
				localStorage.setItem('storage', JSON.stringify(this.comments));
			},
		},
		created() {
			this.comments = (localStorage.getItem('storage')?JSON.parse(localStorage.getItem('storage')):[]);
		}
	};
</script>

<style>
	.el-row {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	.el-col {
		border-radius: 4px;
	}
	.grid-content {
		border-radius: 4px;
		min-height: 36px;
	}
</style>